<template>
  <div class="r_bg">
    <!--  导航栏-->
    <NavBar class="r_nav">
      <i slot="left" class="el-icon-back" @click="back"></i>
      <div slot="center">性格密码大揭秘</div>
    </NavBar>
    <div>
      <!--     性格展示 -->
      <el-card shadow="never" class="r_card" style="margin-top: 44px">
        <div id="myRadar" :style="{height:'300px',width: '100%',background:'#fff'}"></div>
      </el-card>
      <!--      详情解析-->
      <el-card class="r_card">
        <div slot="header">
          您的孩子可能是：<span v-for="item in arr">{{ item.title + ' ' }}</span>
        </div>
        <div v-for="item in arr" style="margin: 15px 0 0 0 ; white-space: pre-wrap;line-height: 30px">
          {{ item.title + ':' + '\n' }}{{ item.body }}
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/navbar/NavBar";

var echarts = require('echarts/lib/echarts'); //基础实例 注意不要使用import
require('echarts/lib/chart/radar'); //按需引入
require('echarts/lib/component/tooltip');
require('echarts/lib/component/legend');
require('echarts/lib/component/title');

export default {
  name: "Result",
  data() {
    return {
      form: [],
      arr: []
    }
  },
  created() {
    this.form = this.$route.params.toResultForm;
    if (this.form === undefined) {
      this.$message.warning('请重新进行测试')
      this.form = [
        {
          num: 0,
          title: '',
          body: ''
        },
        {
          num: 0,
          title: '',
          body: ''
        },{
          num: 0,
          title: '',
          body: ''
        },{
          num: 0,
          title: '',
          body: ''
        },{
          num: 0,
          title: '',
          body: ''
        }
      ]
      setTimeout(()=> {
        this.$router.push('/survey')
      },3000)
    }
    this.getMessage()
    this.$nextTick(() => {
      this.getRadarTest()
      window.onresize = this.myRadarEchars.resize;

    })
  },
  methods: {
    getRadarTest() {
      var option = {
        title: {
          text: "宝宝性格图表",
          textStyle: {
            fontSize: 16
          }
        },
        tooltip: {},
        legend: {
          orient: 'horizontal',
          top: "bottom",
          padding: [5, 10],
          itemHeight: 14,
          itemWidth: 30,
          itemGap: 15,
          selectedMode: false,
          textStyle: {
            fontSize: 12
          },
          data: ["实际性格"]
        },
        radar: {
          center: ["50%", "50%"],
          radius: '60%',
          name: {
            show: true,
            fontSize: 14,
            fontStyle: 'normal',
            textStyle: {
              color: '#999',
              borderRadius: 3
            }
          },
          indicator: [
            {name: this.form[0].title, max: 3},
            {name: this.form[1].title, max: 3},
            {name: this.form[2].title, max: 3},
            {name: this.form[3].title, max: 3},
            {name: this.form[4].title, max: 3},
          ]
        },
        series: [
          {
            name: "实际性格值",
            type: "radar",
            data: [
              // {
              //   value: [1.5, 1.5, 1.5, 1.5, 1.5],
              //   name: "标准性格"
              // },
              {
                value: [this.form[0].num, this.form[1].num, this.form[2].num, this.form[3].num, this.form[4].num],
                name: "实际性格"
              }
            ]
          }
        ]
      };
      this.myRadarEchars = echarts.init(document.getElementById("myRadar"));
      this.myRadarEchars.setOption(option)
    },
    back() {
      this.$router.push('/my')
    },
    getMessage() {
      //排序
      var temp = [], i = 0, j = 0;
      for (j = 0; j < 4; j++) {
        for (i = 0; i < 4 - j; i++) {
          if (this.form[i].num < this.form[i + 1].num) {
            temp = this.form[i];
            this.form[i] = this.form[i + 1];
            this.form[i + 1] = temp;
          }
        }
      }
      temp = []
      //判断最大的是哪些数组
      for (var n = 0; n < 4; n++) {
        if (this.form[n].num === this.form[n + 1].num) {
          temp[n] = this.form[n].num
          temp[n + 1] = this.form[n + 1].num
          if (Math.max.apply(Math, temp) === this.form[n].num) {
            this.arr[n] = this.form[n]
            this.arr[n + 1] = this.form[n + 1]
          }
        }else {
          this.arr[0] = this.form[0]
        }
      }
    }
  },
  components: {
    NavBar
  }
}
</script>

<style lang="less" scoped>
.r_bg {
  background-color: #fafafa;
  height: 100vh;
  position: relative;
  width: 100%;
}

.r_nav {
  background-color: #fafafa;
  font-weight: bold;
  font-size: 25px;
  color: black;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 9;
}

.r_nav i {
  font-size: 30px;
}

.r_card {
  margin: 15px 15px;
  border-radius: 10px;
}
</style>
